.button {
  width: 100px;
  height: 30px;
  padding: 5px;
  margin-bottom: 15px;
  color: beige;
  background-color: rgb(4, 4, 94);
  border-radius: 6%;
  border: none;
}
.image {
  width: 300px;
  height: 400px;
}
.overview {
  width: 600px;
  text-align: justify;
}
.title {
  color: rgb(4, 4, 94);
}
.genres {
  font-size: 14px;
}
.filmThumb {
  display: flex;
  border-bottom: 2px solid grey;
}
.info {
  padding-left: 30px;
}
